.join__wrapper {
	margin-bottom: 240px;
	display: flex;
	flex-direction: column;
	// align-items: center;
	justify-content: center;
	max-width: 549px;
	.join__heading {
		h2 {
			font-style: normal;
			font-weight: 500;
			font-size: 48px;
			line-height: 1.3;
			text-align: center;
			margin-bottom: 25px;
			color: rgba(255, 255, 255, 0.9);
		}
		p {
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 100px;
			font-weight: 500;
			font-size: 18px;
			line-height: 1.65;
			text-align: center;
			max-width: 430px;
			color: rgba(255, 255, 255, 0.55);
		}
	}
}

// поле формы подписки
.join__content-search {
	padding: 10px;
	display: flex;
	align-items: center;
	flex-direction: row;
	justify-content: space-between;
	position: relative;
	border-radius: 15px;
	height: 64px;
	width: 100%;
	background: #283036;
	border-image-source: linear-gradient(90.89deg, rgba(255, 255, 255, 0.1) -1.67%, rgba(255, 255, 255, 0) 103.73%);
	input {
		position: relative;
		border: none;
		padding-left: 24px;
		width: 100%;
		height: 64px;
		background: transparent;
		border: 1.5px solid transparent;
		display: block;
		color: rgba(255, 255, 255, 0.38);
		font-size: 14px;
		&:focus {
			border: none;
			outline: none;
		}
	}
}
.join__content-btn {
	display: block;
	bottom: 0;
	background: rgba(255, 110, 48, 1);
	min-width: 130px;
	height: 48px;
	padding: 0px 36px;
	border-radius: 8px;
	border: none;
}
