.footer {
	padding-top: 70px;
	padding-bottom: 98px;
}
.footer__wrapper {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: flex-start;
	justify-content: space-between;
}
.column {
	display: flex;
	flex-direction: column;
}
.footer__copyright {
	max-width: 260px;
	.footer__logo {
		display: flex;
		align-items: center;
		justify-content: space-between;
		max-width: 140px;
		margin-bottom: 27px;
		img {
			background: #ff6e30;
			border-radius: 8px;
			display: block;
			padding: 9px;
			height: 38px;
			width: 38px;
		}
		span {
			font-weight: 600;
			font-size: 22px;
			line-height: 1.5;

			color: rgba(255, 255, 255, 0.9);
		}
	}
	.footer__copy {
		margin-bottom: 21px;
	}
	.footer__desc {
		font-size: 14px;
		line-height: 1.8;

		color: rgba(255, 255, 255, 0.55);
	}
}

.column {
	.footer-heading {
		font-weight: 500;
		font-size: 20px;
		line-height: 30px;
		color: rgba(255, 255, 255, 0.9);
		margin-bottom: 20px;
	}
	li {
		margin-bottom: 10px;
		list-style: none;
		a {
			font-weight: 500;
			font-size: 16px;
			line-height: 24px;
			color: rgba(255, 255, 255, 0.55);
			text-decoration: none;
		}
		address {
			font-style: normal;
			font-weight: 500;
			font-size: 16px;
			line-height: 24px;
			color: rgba(255, 255, 255, 0.55);
		}
	}
}
