.newcomer {
	padding-top: 15px;
	padding-bottom: 15px;
	margin-bottom: 200px;

	.newcomer__wrapper {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		align-items: flex-start;
	}

	.newcomer__left {
		margin-right: auto;
		max-width: 600px;
	}
	.newcomer__head {
		font-weight: 500;
		font-size: 48px;
		line-height: 1.3;
		color: rgba(255, 255, 255, 0.9);
		margin-bottom: 30px;
	}
	.newcomer__text {
		max-width: 430px;
		font-weight: 500;
		font-size: 18px;
		line-height: 1.7;
		color: rgba(255, 255, 255, 0.55);
		margin-bottom: 60px;
	}
	.newcomer__more {
		display: block;
		span {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 198px;
			padding: 16px 0px;
			background: #ff6e30;
			border-radius: 8px;
			font-weight: 500;
			font-size: 18px;
			line-height: 1.5;
			color: #ffffff;
		}
	}
	.newcomer__right {
		padding-top: 20px;
		padding-bottom: 20px;
		max-width: 600px;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		row-gap: 50px;
		align-items: center;
		justify-content: center;
	}
	.newcomer__app {
		margin-right: 5px;
		position: relative;
		&:hover:before {
			position: absolute;
			content: "";
			width: 65px;
			height: 65px;
			box-shadow: 230px 0px rgba(255, 110, 48, 1);
			border-radius: 50%;
			transition: box-shadow 1000ms ease-in-out;
		}
	}
	.newcomer__body {
		padding: 28px 22px;
		max-width: 295px;
		max-height: 295px;
		border-radius: 15px;
		display: flex;
		flex-direction: column;
		&:hover {
			background: rgba(40, 48, 54, 0.6);
			backdrop-filter: blur(50px);
			border-right: 1px solid rgba(255, 255, 255, 0.1);
			border-left: 1px solid rgba(255, 255, 255, 0.1);
			border-bottom: 1px solid rgba(255, 255, 255, 0.1);
			border-top: 1px solid rgba(255, 255, 255, 0.1);
			transform: translateY(-10px);
			transition: transform 500ms ease-in-out, background 500ms ease-in-out;
		}
	}
	.newcomer__title {
		display: flex;
		margin-bottom: 30px;
	}
	.newcomer__image-wrapper {
		width: 64px;
		height: 64px;

		margin-right: 30px;
	}
	.newcomer__heading {
		h2 {
			font-weight: 500;
			font-size: 24px;
			line-height: 1.5;
			color: rgba(255, 255, 255, 0.78);
			margin-bottom: 3px;
		}
		span {
			font-size: 18px;
			line-height: 27px;
			color: rgba(255, 165, 55, 0.55);
		}
	}
	.newcomer__description {
		font-size: 14px;
		line-height: 30px;
		/* or 214% */
		color: rgba(255, 255, 255, 0.78);
		margin-bottom: 37px;
	}
	.newcomer__action {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
	}
	.action__left {
		display: flex;
		align-items: center;
		i {
			font-size: 28px;
			padding: 7px;
			display: block;
			color: rgba(255, 255, 255, 0.38);
			border-radius: 8px;
			cursor: pointer;
			&:first-child {
				margin-right: 12px;
				&:hover {
					color: #fd3b41;
					transition: color 800ms ease-in-out, color 800ms ease-in-out;
				}
			}
			&:last-child {
				&:hover {
					color: rgba(255, 110, 48, 1);
					transition: color 800ms ease-in-out, color 800ms ease-in-out;
				}
			}
			&:hover {
				background: #283036;
				transition: background 800ms ease-in-out, color 800ms ease-in-out;
			}
		}
	}
	.action__right {
		span {
			display: block;
			padding: 9px 35px;
			border-radius: 8px;
			color: rgba(255, 255, 255, 0.55);
			font-weight: 600;
			font-size: 16px;
			line-height: 1.5;
			&:hover {
				background: #ff6e30;
				color: rgba(255, 255, 255, 1);
				transition: background 800ms ease-in-out, color 800ms ease-in-out, color 800ms ease-in-out,
					color 800ms ease-in-out;
			}
			&::first-letter {
				text-transform: uppercase;
			}
		}
	}
}
