.tools {
	position: relative;
	display: block;
	padding-bottom: 200px;
	.tools__more {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.tools__header {
		text-align: center;
		font-weight: 500;
		margin-bottom: 90px;
		h1 {
			font-size: 48px;
			font-weight: 500;
			line-height: 1.5;
			margin-bottom: 25px;
		}
		p {
			display: block;
			width: 410px;
			font-size: 18px;
			line-height: 1.7;
			color: rgba(255, 255, 255, 0.55);
			margin: 0 auto;
			padding: 0 10px;
		}
	}
	.btn {
		position: relative;
		text-align: center;
		cursor: pointer;
		span {
			border: 1px solid rgba(255, 255, 255, 0.1);
			width: 184px;
			font-weight: 500;
			font-size: 20px;
			line-height: 2;
			padding: 12px 0;
			position: relative;
			display: inline-block;
			text-decoration: none;
			border-radius: 8px;
			background: #1e252b;
			color: rgba(255, 110, 48, 1);
			transition: background 500ms ease-in-out, color 500ms ease-in-out;
			&:hover {
				color: #fff;
				background: rgba(255, 110, 48, 1);
			}
		}
	}
}
.active [data-view="false"] {
	display: block;
}
[data-view="true"] {
	display: display;
}
[data-view="false"] {
	display: none;
}
.app__wrapper {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	row-gap: 100px;
	margin-bottom: 100px;

	.app {
		position: relative;
		.app__body {
			min-height: 295px;
			max-width: 390px;
			display: flex;
			flex-direction: column;
			border-radius: 15px;
			padding: 29px;
		}

		.app__image-wrapper {
			display: flex;
			align-items: center;
			overflow: hidden;
			height: 65px;
			width: 65px;
			object-fit: contain;
			margin-right: 30px;
			margin-bottom: 30px;
			img {
				display: block;
				object-fit: contain;
			}
		}
		.app__title {
			display: flex;
			flex-direction: row;
			align-items: flex-start;
			margin-right: auto;
			.app__heading {
				margin-left: 30px;
				line-height: 1.5;
				h2 {
					font-size: 24px;
				}
				span {
					font-size: 18px;
					color: rgba(255, 165, 55, 0.55);
				}
			}
		}
		.app__description {
			margin-bottom: 30px;
			margin-bottom: auto;
		}
	}
}
.app__action {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	.action__left {
		display: flex;
		align-items: center;
		i {
			font-size: 28px;
			padding: 7px;
			display: block;
			color: rgba(255, 255, 255, 0.38);
			border-radius: 8px;
			cursor: pointer;
			&:first-child {
				margin-right: 12px;
			}
		}
	}

	span {
		display: block;
		padding: 9px 35px;
		border-radius: 8px;
		color: rgba(255, 255, 255, 0.55);
		font-weight: 600;
		font-size: 16px;
		line-height: 1.5;
		&::first-letter {
			text-transform: uppercase;
		}
	}
}
