.app__action {
	.action__left {
		i {
			&:first-child {
				&:hover {
					color: #fd3b41;
					transition: color 800ms ease-in-out, color 800ms ease-in-out;
				}
			}
			&:last-child {
				&:hover {
					color: rgba(255, 110, 48, 1);
					transition: color 800ms ease-in-out, color 800ms ease-in-out;
				}
			}
			&:hover {
				background: #283036;
				transition: background 800ms ease-in-out, color 800ms ease-in-out;
			}
		}
	}
	span {
		&:hover {
			background: #ff6e30;
			color: rgba(255, 255, 255, 1);
			transition: background 800ms ease-in-out, color 800ms ease-in-out, color 800ms ease-in-out,
				color 800ms ease-in-out;
		}
	}
}
.app__wrapper {
	.app {
		&:hover::before {
			position: absolute;
			content: "";
			width: 65px;
			height: 65px;
			box-shadow: 320px 20px rgba(255, 110, 48, 1);
			border-radius: 50%;
			transition: box-shadow 1000ms ease-in-out;
		}
		.app__body {
			&:hover {
				background: rgba(40, 48, 54, 0.6);
				backdrop-filter: blur(35px);
				border-right: 1px solid rgba(255, 255, 255, 0.1);
				border-left: 1px solid rgba(255, 255, 255, 0.1);
				border-bottom: 1px solid rgba(255, 255, 255, 0.1);
				border-top: 1px solid rgba(255, 255, 255, 0.1);
				transform: translateY(-10px);
				transition: transform 500ms ease-in-out, background 500ms ease-in-out;
			}
		}
	}
}
