.staffer {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 230px;
	.staffer__wrapper {
		height: 600px;
	}
}
.slider {
	position: relative;
	width: 1140px;
	height: 560px;
	margin: 20px;
	overflow: hidden;

	.slide {
		display: flex;
		align-items: center;
		position: absolute;
		width: 100%;
		height: 100%;
		padding-left: 184px;
		padding-right: 185px;
		-webkit-clip-path: circle(0% at 0 50%);
		clip-path: circle(0% at 0 50%);
		.staffer__image-wrapper {
			position: relative;
			top: 0;
			left: 0;
			width: 393px;
			height: 100%;
			overflow: hidden;
			background-repeat: no-repeat;
			background-size: 650px;
			background-position: center -230px;
			z-index: 1;
			img {
				position: absolute;
				top: 0;
				right: -25%;
				width: 632px;
				height: 492px;
				object-fit: contain;
			}
		}

		.staffer__text {
			position: relative;
			font-family: Roboto, sans-serif;
			max-width: 393px;
			letter-spacing: 0.015em;
			font-style: normal;
			font-weight: 200;
			font-size: 14px;
			line-height: 30px;
			&::before {
				content: "";
				position: absolute;
				bottom: 25%;
				left: -50px;
				width: 103px;
				height: 320px;
				background: url("../img/sprite.svg#quotation_marks");
				background-size: contain;
				background-position: center;
				background-repeat: no-repeat;
			}
		}
		.staffer__info {
			position: relative;
			margin-left: auto;
			display: flex;
			flex-direction: column;
			padding-left: 22px;
			padding-right: 22px;
			justify-content: center;
			margin-top: 372px;
			width: 265px;
			height: 91px;
			background: rgba(40, 48, 54, 0.2);
			backdrop-filter: blur(25px);
			border-radius: 8px;
			text-align: left;
			border-right: 0.5px solid rgba(255, 255, 255, 0.1);
			border-left: 0.5px solid rgba(255, 255, 255, 0.1);
			border-bottom: 0.5px solid rgba(255, 255, 255, 0.1);
			border-top: 0.5px solid rgba(255, 255, 255, 0.1);

			h2 {
				font-weight: 500;
				font-size: 26px;
				line-height: 39px;
				letter-spacing: 0.015em;
				color: rgba(255, 253, 253, 0.78);
			}
			p {
				font-size: 16px;
				line-height: 24px;
				color: rgba(255, 253, 253, 0.55);
			}
		}
		.slide__left {
			width: 50%;
			height: 492px;
		}
		.slide__right {
			display: flex;
			align-items: center;
			width: 50%;
			height: 492px;
			padding-left: 41px;
		}
	}
}
.slider .slide.active {
	-webkit-clip-path: circle(150% at 0 50%);
	clip-path: circle(150% at 0 50%);
	-webkit-transition: 2s;
	-o-transition: 2s;
	transition: 2s;
}
.navigation {
	display: flex;
	width: 100%;
	height: 600px;
	align-items: center;
	justify-content: space-between;
	opacity: 0;
	transition: opasity 0.5s ease;
}
.slider:hover .navigation {
	opacity: 1;
}
.prev-btn,
.next-btn {
	z-index: 999;
	font-size: 24px;
	padding: 10px;
	cursor: pointer;

	color: rgba(255, 255, 255, 0.28);
}

.navigation-vizibility {
	z-index: 999;
	display: flex;
	justify-content: center;
	.slide-icon {
		z-index: 999;
		background: #283036;
		width: 44px;
		height: 8px;
		-webkit-transform: translateY(-50px);
		-ms-transform: translateY(-50px);
		transform: translateY(-50px);
		margin: 0 6px;
		border-radius: 4px;
	}
	.slide-icon.active {
		background: rgba(255, 110, 48, 1);
	}
}

// дизайнер чтоб тебе тепло было
// внешний круг
.circle-out {
	position: absolute;
	width: 380px;
	height: 380px;
	z-index: -9;
	transform: translate(-2%, 26%);
	border-radius: 50%;
	background: rgba(40, 48, 54, 0.4);
	border: 1px solid rgba(255, 255, 255, 0.03);
	backdrop-filter: blur(25px);
	border-right: 0.5px solid rgba(255, 255, 255, 0.1);
	border-left: 0.5px solid rgba(255, 255, 255, 0.1);
	border-bottom: 0.5px solid rgba(255, 255, 255, 0.1);
	border-top: 0.5px solid rgba(255, 255, 255, 0.1);
}
// внутренний круг
.circle-inner {
	position: absolute;
	width: 305.65px;
	height: 305.65px;
	z-index: -8;
	transform: translate(9%, 43%);
	border-radius: 50%;
	background: rgba(40, 48, 54, 0.4);
	border: 1px solid rgba(255, 255, 255, 0.03);
	backdrop-filter: blur(25px);
	border-right: 0.1px solid rgba(255, 255, 255, 0.1);
	border-left: 0.1px solid rgba(255, 255, 255, 0.1);
	border-bottom: 0.1px solid rgba(255, 255, 255, 0.1);
	border-top: 0.1px solid rgba(255, 255, 255, 0.1);
}
.circle-dot-1 {
	position: relative;
	width: 100%;
	&::before {
		content: "";
		position: absolute;
		z-index: -100;
		width: 43px;
		height: 43px;
		box-shadow: 53px 149px rgba(179, 69, 241, 1);
		border-radius: 50%;
	}
}
.circle-dot-2 {
	position: relative;
	width: 100%;
	&::before {
		content: "";
		position: absolute;
		z-index: -100;
		width: 43px;
		height: 43px;
		backdrop-filter: blur(25px);
		box-shadow: 320px 390px rgba(255, 110, 48, 1);
		border-radius: 50%;
	}
}
