.container {
  margin: 0 auto;
  padding: 0 15px;
}

@media (min-width: 575.98px) {
  .container {
    padding: 0 calc(50% - 270px);
  }
}

@media (min-width: 767.98px) {
  .container {
    padding: 0 calc(50% - 360px);
  }
}

@media (min-width: 991.98px) {
  .container {
    padding: 0 calc(50% - 480px);
  }
}

@media (min-width: 1199.98px) {
  .container {
    padding: 0 calc(50% - 590px);
  }
}

.navigation {
  display: flex;
  flex-flow: row wrap;
  /* Это выравнивает элементы по правому краю вдоль основной оси */
  justify-content: flex-end;
}

@media (max-width: 800px) {
  .navigation {
    /* На средних экранах мы выравниваем меню по центру за счет равномерного распределения доступного пространства между элементами */
    justify-content: space-around;
  }
}

@media (max-width: 500px) {
  .navigation {
    flex-wrap: wrap;
    /* На маленьких экранах мы меняем направление основной оси с горизонтального (строка) на вертикальное (колонка) */
    flex-direction: column;
  }
}

.header {
  background: url("../img/bg-header.png") 60% top no-repeat;
  background-size: cover;
  min-height: 700px;
  padding-bottom: 20px;
  margin-bottom: 200px;
}

.header .nav-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
  min-height: 100px;
  padding-top: 26px;
  padding-bottom: 26px;
  margin-bottom: 78px;
}

.nav__logo {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
}

.nav__logo svg {
  width: 38px;
  height: 38px;
  padding: 8px;
  background-color: #ff6e30;
  border-radius: 8px;
  margin-right: 12px;
}

.nav__menu {
  display: inline-flex;
  width: 450px;
}

.nav__menu a {
  display: inline-block;
  color: rgba(255, 255, 255, 0.55);
  font-size: 16px;
  transition: color 300ms ease-in-out;
}

.nav__menu li:hover a {
  color: #fff;
  transition: color 300ms ease-in-out;
}

.menu__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 42px;
}

.nav__login {
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
}

.login__label {
  font-size: 16px;
  margin-right: 40px;
}

.login__label a {
  color: rgba(255, 255, 255, 0.55);
  transition: color 300ms ease-in-out;
}

.login__label:hover a {
  color: #fff;
  transition: color 300ms ease-in-out;
}

.login__btn {
  width: 118px;
  height: 48px;
  background-color: #ff6e30;
  outline: none;
  border: none;
  border-radius: 8px;
  color: #fdf5ff;
}

.hero__content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 600px;
}

.hero__content-heading {
  margin-bottom: 30px;
  font-weight: 500;
  font-size: 50px;
  line-height: 140%;
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.4;
}

.hero__content-heading span {
  color: #ff6e30;
}

.hero__content-paragrath {
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  text-align: left;
  max-width: 487px;
  color: rgba(255, 255, 255, 0.55);
  line-height: 1.7;
  margin-bottom: 60px;
  font-weight: 400;
}

.hero__content-search {
  padding: 10px;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  border-radius: 15px;
  height: 64px;
  max-width: 500px;
  background: #283036;
  border-image-source: linear-gradient(90.89deg, rgba(255, 255, 255, 0.1) -1.67%, rgba(255, 255, 255, 0) 103.73%);
  margin-bottom: 110px;
}

.hero__content-search::before {
  content: "";
  position: absolute;
  background: url(../img/sprite.svg#search);
  background-repeat: no-repeat;
  background-position: center;
  left: 24px;
  top: 24px;
  transform: translate(-12px, -4px);
  display: block;
  width: 24px;
  height: 24px;
}

.hero__content-search input {
  position: relative;
  border: none;
  padding-left: 55px;
  width: 100%;
  height: 64px;
  background: transparent;
  border: 1.5px solid transparent;
  display: block;
  color: rgba(255, 255, 255, 0.38);
  font-size: 14px;
}

.hero__content-search input:focus {
  border: none;
  outline: none;
}

.hero__content-btn {
  display: block;
  bottom: 0;
  background: #ff6e30;
  width: 130px;
  height: 48px;
  border-radius: 8px;
  border: none;
}

.social__list {
  display: flex;
