.brand {
	margin-bottom: 200px;
}
.glass-wrapper {
	position: relative;
	padding: 55px 83px;
	min-height: 253px;
	width: 100%;
	background: rgba(44, 53, 61, 0.2);
	backdrop-filter: blur(40px);
	border-right: 1px solid rgba(255, 255, 255, 0.1);
	border-left: 1px solid rgba(255, 255, 255, 0.1);
	border-bottom: 1px solid rgba(255, 255, 255, 0.1);
	border-top: 1px solid rgba(255, 255, 255, 0.1);
	border-radius: 25px;
}
.brand__glass-wrapper {
	position: relative;
	&::before {
		position: absolute;
		content: "";
		width: 65px;
		height: 65px;
		top: 2px;
		left: 20px;
		background: rgba(255, 110, 48, 1);
		border-radius: 50%;
	}
}
.brand__heading {
	text-align: center;
	font-weight: 500;
	font-size: 24px;
	line-height: 1.5;
	color: rgba(255, 255, 255, 0.8);
	margin-bottom: 55px;
}
.brand__shells {
	min-height: 51px;
}
.brand__list {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
}
